<template>
    <div class="">


        <div class="row q-mt-md q-col-gutter-xs">
            <div class="col col-xs-12 col-sm-12 col-md-12 self-center q-px-md">
                <div class="q-mx-md text-h6 text-center text-weight-bolder">{{ halaman }}</div>
                <!-- <q-separator class="" color="orange" inset /> -->
            </div>

            <div class="col col-xs-12 col-sm-12 col-md-4 q-px-md self-center">
                <q-select filled v-model="model" use-input input-debounce="0" label="Masyarakat / OPD / Sub OPD" :options="options"
                    @filter="filterFn" :dense="true">
                    <template v-slot:no-option>
                        <q-item>
                            <q-item-section class="text-grey">
                                No results
                            </q-item-section>
                        </q-item>
                    </template>
                </q-select>
            </div>
            <div class="col col-xs-12 col-sm-12 col-md-4 q-px-md self-center">
                <q-select filled v-model="model" use-input input-debounce="0" label="Simple filter" :options="options"
                    @filter="filterFn" :dense="true">
                    <template v-slot:no-option>
                        <q-item>
                            <q-item-section class="text-grey">
                                No results
                            </q-item-section>
                        </q-item>
                    </template>
                </q-select>
            </div>
            <div class="col col-xs-12 col-sm-12 col-md-3 q-px-md self-center">
                <q-input class="self-center" filled v-model="text" label="Pencarian" :dense="true">
                    <template v-slot:append>
                        <q-icon v-if="text !== ''" name="close" @click="text = ''" class="cursor-pointer" />
                        <q-icon name="search" />
                    </template>
                </q-input>
            </div>
            <div class="col col-xs-12 col-sm-12 col-md-1 q-px-md self-center">
                <q-btn round @click="mdl_add = true, funcAwaitAdd()" color="black" icon="add" />
            </div>
        </div>







        <div class="row q-mt-md q-mx-md">
            <!-- <div class="col col-xs-12 col-sm-12 col-md-12 self-center">
                <div class="text-h6">Halaman</div>
            </div> -->

            <div class="col col-xs-12 col-sm-12 col-md-12 self-center">
                <q-markup-table class="my-sticky-dynamic" flat bordered separator="cell">
                    
                    <thead>
                        <tr class="bg-blue-1">
                            <th class="text-center" style="width:10%;">No</th>
                            <th class="text-center" style="width:80%;">Uraian</th> 
                            <th class="text-center" style="width:10%;">Act</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in listData" :key="item.id">
                            <td class="text-center">{{index+1}}.</td>
                            <td class="text-left text-no-wrap" > <span style="font-size:12px;">{{item.uraian}}</span></td> 
                            <td class="text-center">
                                <div>
                                    <q-btn-group push>
                                        <!-- <q-btn push color="primary" icon="edit" size="sm"/> -->
                                        <q-btn push color="warning" icon="edit" class="text-black" size="sm" @click="mdl_edit=true, getData(item)">
                                            <q-tooltip class="bg-blue text-body2" size="sm" anchor="top middle" self="bottom middle" :offset="[10, 10]"> Ubah </q-tooltip>                                            
                                        </q-btn>
                                        <q-btn push color="negative" icon="delete" size="sm" @click="mdl_del=true, getData(item)">
                                            <q-tooltip class="bg-blue text-body2" size="sm" anchor="top middle" self="bottom middle" :offset="[10, 10]"> Hapus </q-tooltip>
                                        </q-btn>
                                    </q-btn-group> 
                                </div>                                
                            </td>
                        </tr>
                    </tbody>
                </q-markup-table>
            </div>
            
 


        </div>

        <q-dialog persistent v-model="mdl_add" >
            <q-card style="width: 80vw">
                <q-card-section class="bg-cyan">
                    <div class="text-overline">INPUT</div>
                </q-card-section>

                <q-separator />
                <form @submit.prevent="addData()">
                    <q-card-section style="max-height: 50vh;" class="scroll">
                        <div class="row q-col-gutter-xs">
                            <div class="col-md-12 col-sm-12 col-xs-12">
                                <q-input class="self-center" filled v-model="form.uraian" label="Nama Kantor" :dense="true"/>  
                            </div> 
                            <div class="col-md-12 col-sm-12 col-xs-12">
                                <!-- list_menu -->
                                <ul v-for="data in list_menu">
                                    <li :key="data._id">
                                        <span class="h_menu">{{data.title}}</span>  
                                        <div v-if="!data.type" >
                                            <q-checkbox v-model="data.readx" label="Lihat" color="cyan" />
                                            <q-checkbox v-model="data.addx" label="Tambah" color="teal" />
                                            <q-checkbox v-model="data.updatex" label="Edit" color="orange"/>
                                            <q-checkbox v-model="data.deletex" label="Hapus" color="red"/> 
                                        </div> 
                                    </li>
                                    <ul v-for="data1 in data.subItem">
                                        <li :key="data1._id">
                                            <span class="h_menu">{{data1.title}}</span>  
                                            <div v-if="!data1.type" >
                                                <q-checkbox v-model="data1.readx" label="Lihat" color="cyan" />
                                                <q-checkbox v-model="data1.addx" label="Tambah" color="teal" />
                                                <q-checkbox v-model="data1.updatex" label="Edit" color="orange"/>
                                                <q-checkbox v-model="data1.deletex" label="Hapus" color="red"/> 
                                            </div> 
                                        </li>
                                        <ul v-for="data2 in data1.subItem">
                                            <li :key="data2._id">
                                                <span class="h_menu">{{data2.title}}</span>  
                                                <div v-if="!data2.type" >
                                                    <q-checkbox v-model="data2.readx" label="Lihat" color="cyan" />
                                                    <q-checkbox v-model="data2.addx" label="Tambah" color="teal" />
                                                    <q-checkbox v-model="data2.updatex" label="Edit" color="orange"/>
                                                    <q-checkbox v-model="data2.deletex" label="Hapus" color="red"/> 
                                                </div> 
                                            </li>
                                        </ul>                                
                                    </ul>                                
                                </ul>                                
                                
                                
                            </div> 
                        </div>
                    </q-card-section>
                    <q-separator />
                    <q-card-actions align="right"> 
                        <q-btn-group push>
                            <q-btn push type="submit" color="primary" label="Tambah" icon="add" size="sm"/>
                            <q-btn push color="negative" label="Batal/Keluar" icon="close" size="sm" v-close-popup/> 
                        </q-btn-group>                    
                    </q-card-actions>
                </form>

            </q-card>
        </q-dialog>

        <q-dialog persistent v-model="mdl_edit" >
            <q-card style="width: 80vw">
                <q-card-section class="bg-amber">
                    <div class="text-overline">EDIT</div>
                </q-card-section>

                <q-separator />
                <form @submit.prevent="editData(form)">
                    <q-card-section style="max-height: 50vh;" class="scroll">
                        <div class="row q-col-gutter-xs">
                            <div class="col-md-12 col-sm-12 col-xs-12">
                                <q-input class="self-center" filled v-model="form.uraian" label="Nama Kantor" :dense="true"/>  
                            </div> 
                            <div class="col-md-12 col-sm-12 col-xs-12">
                                <!-- list_menu -->
                                <ul v-for="data in list_menu">
                                    <li :key="data._id">
                                        <span class="h_menu">{{data.title}}</span>  
                                        <div v-if="!data.type" >
                                            <q-checkbox v-model="data.readx" label="Lihat" color="cyan" :true-value="1" :false-value="0"/>
                                            <q-checkbox v-model="data.addx" label="Tambah" color="teal" :true-value="1" :false-value="0"/>
                                            <q-checkbox v-model="data.updatex" label="Edit" color="orange" :true-value="1" :false-value="0"/>
                                            <q-checkbox v-model="data.deletex" label="Hapus" color="red" :true-value="1" :false-value="0"/> 
                                        </div> 
                                    </li>
                                    <ul v-for="data1 in data.subItem">
                                        <li :key="data1._id">
                                            <span class="h_menu">{{data1.title}}</span>  
                                            <div v-if="!data1.type" >
                                                <q-checkbox v-model="data1.readx" label="Lihat" color="cyan" :true-value="1" :false-value="0" />
                                                <q-checkbox v-model="data1.addx" label="Tambah" color="teal" :true-value="1" :false-value="0" />
                                                <q-checkbox v-model="data1.updatex" label="Edit" color="orange" :true-value="1" :false-value="0" />
                                                <q-checkbox v-model="data1.deletex" label="Hapus" color="red" :true-value="1" :false-value="0" /> 
                                            </div> 
                                        </li>
                                        <ul v-for="data2 in data1.subItem">
                                            <li :key="data2._id">
                                                <span class="h_menu">{{data2.title}}</span>  
                                                <div v-if="!data2.type" >
                                                    <q-checkbox v-model="data2.readx" label="Lihat" color="cyan" :true-value="1" :false-value="0" />
                                                    <q-checkbox v-model="data2.addx" label="Tambah" color="teal" :true-value="1" :false-value="0" />
                                                    <q-checkbox v-model="data2.updatex" label="Edit" color="orange" :true-value="1" :false-value="0"/>
                                                    <q-checkbox v-model="data2.deletex" label="Hapus" color="red" :true-value="1" :false-value="0"/> 
                                                </div> 
                                            </li>
                                        </ul>                                
                                    </ul>                                
                                </ul>                                
                            </div> 
                        </div>
                    </q-card-section>
                    <q-separator />
                    <q-card-actions align="right"> 
                        <q-btn-group push>
                            <q-btn push type="submit" color="primary" label="Edit" icon="add" size="sm"/>
                            <q-btn push color="negative" label="Batal/Keluar" icon="close" size="sm" v-close-popup/> 
                        </q-btn-group>                    
                    </q-card-actions>
                </form>

            </q-card>
        </q-dialog>

        <q-dialog v-model="mdl_del" persistent>
            <q-card>
                <q-card-section class="row items-center">
                <q-avatar icon="signal_wifi_off" color="primary" text-color="white" />
                <span class="q-ml-sm">You are currently not connected to any network.</span>
                </q-card-section>

                <form @submit.prevent="removeData(form)">
                    <q-card-actions align="right">
                        <q-btn flat label="Hapus" type="submit" color="primary" />
                        <q-btn flat label="Cancel" color="negative" v-close-popup />
                    </q-card-actions>
                </form>

            </q-card>
        </q-dialog>        
        
    </div>
</template>

<script>
// @ is an alias to /src 
import FETCHING from "../../../library/fetching"; 
import { Notify } from 'quasar'


export default {
    data() {
        return {
            mdl_add: false,
            mdl_edit: false,
            mdl_del: false,
            halaman: 'Kelompok User (Menu)',
            options: ['data1', 'data2', 'data3', 'data4'],
            model: '',
            text: '',
            FETCHING : FETCHING,
            form:{
                id          :'',
                uraian      :'', 
            },
            listData:[],
            list_menu : [],

            page_first: 1,
            page_last: 0,
            page_limit : 10,
            cari_value: "",
        }
    },

    methods: {
        filterFn(val, update) {
            if (val === '') {
                update(() => {
                    console.log(val); 
                })
                return
            }
        },

        getView(){
            fetch(this.$store.state.url.URL_DM_KLP_USERS+'view', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify({
                    data_ke             :this.page_first,
                    cari_value          :this.cari_value,
                    page_limit          :this.page_limit,
                })
            })
            .then(res => res.json())
            .then(res_data => { 
                this.listData = res_data.data
                console.log(res_data.data);
                 
            });                
        },

        addData(){ 
            fetch(this.$store.state.url.URL_DM_KLP_USERS+'addData', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify({
                    form      : this.form,
                    list_menu : this.list_menu
                })
            })
            .then(res_data => {  
                this.getView() 
                this.berhasil() 
            }); 
        },
        editData(){ 
            // console.log(this.form);
            fetch(this.$store.state.url.URL_DM_KLP_USERS+'editData', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify({
                    form        : this.form,
                    list_menu   : this.list_menu
                })
            })
            .then(res_data => {  
                this.getView() 
                this.berhasil() 
            }); 
        },
        removeData(){ 
            // console.log(this.form.id);
            fetch(this.$store.state.url.URL_DM_KLP_USERS+'removeData', {
                method: "POST",
                headers: {
                "content-type": "application/json",
                authorization: "kikensbatara " + localStorage.token
                },
                body: JSON.stringify(this.form)
            })
            .then(res => res.json())
            .then(res_data => {  
                this.getView() 
                this.berhasil() 
            }); 
        },

        async getData(data){
            // console.log(data);
            this.form.id        = data.id
            this.form.uraian    = data.uraian          
            this.list_menu      = await this.FETCHING.postMasterMenu(data.id);
        },

        berhasil(){
            Notify.create({
            message: 'BERHASIL',
            // caption: '5 minutes ago',
            icon:'check',
            color: 'secondary' ,
            position:'center',
            timeout: 2000 })
        },


        funcAwaitAdd : async function(){ 
          this.list_menu = await this.FETCHING.getMasterMenu();
        //   console.log(this.list_menu);
        },




        // ================================================ FILTER ================================================ 
        cariProv : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_PRO = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "provinsi", {cari : val})
                //   console.log(this.form.prov);
                //   this.getView()
                }
              })
        },
        cariKab : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_KAB = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "kabupaten", {cari : val, prov_id:this.form.prov  })
                }
              })
        },
        cariKec : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_KEC = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "kecamatan", {cari : val, kab_id:this.form.kab  })
                }
              })
        },
        cariDes : function (val, update, abort) {
              update(async () => {
                console.log(val);
                if (val === '') {}
                else {
                  this.$store.state.L_DES = await FETCHING.FETCH_POST(this.$store.state.url.URL_WILAYAH + "desa", {cari : val, kec_id:this.form.kec  })
                }
              })
        },
        // ================================================ FILTER ================================================ 
    },
    mounted() {
        this.getView()
        
    },

}
</script>
